import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController } from 'ionic-angular';

import { HomeService } from './home.serve';

import { SearchPage } from './search/search';
import { IntroducePage } from './introduce/introduce';
import { DepartmentPage } from './department/department';
import { NewListPage } from './newlist/newlist';

import { Observable, Subscription } from 'rxjs';
declare var echarts: any;
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  items: any = []
  message: any; isInfiniteEnabled: boolean = true;
  @ViewChild('container1') container: ElementRef;//与html中div #container1对应   
  chart: any;
  constructor(public navCtrl: NavController, public Http: HomeService, private elementRef: ElementRef) {

  }
  gotosearch() {
    this.navCtrl.push(SearchPage);
  }
  introduce() {
    this.navCtrl.push(IntroducePage)
  }
  support() {
    window.localStorage.setItem('tech', 'support');
    this.navCtrl.parent.select(2);
  }
  features() {
    window.localStorage.setItem('tech', 'features');
    this.navCtrl.parent.select(2);
  }
  framework() {
    window.localStorage.setItem('teams', 'framework');
    this.navCtrl.parent.select(3);
  }
  culture() {
    window.localStorage.setItem('teams', 'culture');
    this.navCtrl.parent.select(3);
  }
  move() {
    window.localStorage.setItem('project', 'phone');
    this.navCtrl.parent.select(1);
  }
  PC() {
    window.localStorage.setItem('project', 'pc');
    this.navCtrl.parent.select(1);
  }
  department() {
    this.navCtrl.push(DepartmentPage)
  }
  advantage() {
    window.localStorage.setItem('tech', 'advantage');
    this.navCtrl.parent.select(2);
  }
  ionViewWillEnter() {
    let ctx = this.container.nativeElement;
    this.chart = echarts.init(ctx);
    this.chart.setOption({
      grid: {
        top: "5%"
      },
      tooltip: {
        //				trigger: 'axis',
        trigger: 'item',
        tooltip: false,
        backgroundColor: "#f9119f",
        borderColor: "#ffffff",
        borderWidth: 1,
        enterable: true,
        //				b0 x轴坐标 a0曲线标题 c0 y轴坐标
        position: "top",
        formatter: "{c0}k",
        textStyle: {
          color: "#ffffff",
          align: 'center',
          rich: {
            b: {
              lineHeight: 33
            }
          }
        },
        extraCssText: "width:0.57rem;height:0.33rem；line-height:0.33rem",
        axisPointer: {
          type: 'cross',
          label: {
            backgroundColor: '#6a7985',
            formatter: '{value} k',
          }
        },
      },
      xAxis: {
        splitLine: {
          show: true
        },
        type: 'category',
        boundaryGap: false,
        axisTick: {
          length: 0,
        },
        axisLabel: {
          show: true,
          //					margin:15,
          formatter: '{value} k',
          color: 'rgb(0,0,0)',
          fontSize: 14,
        },
        data: ['2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5', '2.5',]
      },
      yAxis: {
        splitLine: {
          show: true
        },
        type: 'value',
        axisTick: {
          length: 0,
        },
        axisLabel: {
          show: true,
          //					margin:15,
          formatter: '{value} k',
          color: 'rgb(0,0,0)',
          fontSize: 14,
        },
        //				nameGap:0,
        //				data: ['0', '2.5k', '5k', '7.5k', '10k',]
      },
      series: [
        {
          legendHoverLink: true,
          name: '资源一',
          type: 'line',
          smooth: true,
          showSymbol: false,
          areaStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#fc129d' },
                { offset: 1, color: '#bb04be' },
              ], false),
              opacity: "1"
            }
          },
          itemStyle: {
            normal: {
              color: "#f58705",//外圈
            },
            emphasis: {
              color: "#ffffff",//内芯
            },
          },
          lineStyle: {
            normal: {
              color: "#fc129d"//边线
            },
          },
          data: [1.4, 1.7, 1.6, 2, 1.8, 2.7, 2.4, 5.5, 2.5, 2.4, 2, 1.8, 3, 2.7, 4, 1.5, 1.4, 1.1, 2.5, 2.3, 4, 2]
        },

      ]
    })
  }
  ionViewDidLoad() {
    this.loadNewsList()

  }
  loadNewsList() {
    this.Http.getHomeList().then(res => this.items = res.content.slice(0, 3))
    // this.Http.getHomeList().subscribe((res)=>{
    //   res.then((res)=>{
    //     this.items = res.content.slice(0,3)
    //   })
    // })
  }
  gotonewlist(param) {
    this.navCtrl.push(NewListPage, {
      params: {
        title: param.title,
        id: param.id
      }
    })
  }
}



