import { Component,OnInit } from '@angular/core';
import { NavParams } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';

// import { DATA_TEAMS_MEMBER } from './data_teams_member';

declare var $:any;
@Component({
    templateUrl: 'teams_view.html',
})
export class TeamsViewPage implements OnInit {
	//当前类型的团队成员
	// teams_member: any;
	constructor(
		private navParams: NavParams,
		public loadingCtrl: LoadingController
	){}
 	ngOnInit() {
//		this.teams_member = DATA_TEAMS_MEMBER[this.navParams.get('type')];
    }

	ionViewWillEnter(){ 
		//将所有人员加上 卡片式翻页效果
		$('.hub-slider-slides ul').hubSlider({
			selector: $('li'),
			button: {
				next: $('.hub-slider-slides ul li'),
				prev: $('.hub-slider-slides ul li')
			},
			transition: '0.7s',
			startOffset: 30,
			auto: false,
			// time:100000000 // secondly
		});
		
	}
}


